section .cart {
  position: relative;
  border: 1px solid #ccc;
}
.amount span {
  color: #333;
  padding: 0 20px;
}
.amount .count {
  width: 40px;
  height: 30px;
  border: 1px solid #555;
  outline: none;
  background: transparent;
  cursor: pointer;
}
.delete {
  position: absolute;
  top: 0;
  right: 5px;
  color: black;
  cursor: pointer;
  font-weight: 900;
}
.total {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.total > button {
  text-decoration: none;
  background: #333;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 10px 25px;
  margin: 0 25px;
}
.total h3 {
  margin-right: 50px;
  color: black;
}

.modal input {
  border: 0;
  font-family: inherit;
  margin-left: 1px;
  height: 35px;
  font-size: 16px;
}
